import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import GDLogo from "../../assets/images/logo.png";

import "./Navbar.css";

function Navbar({ location, ...props }) {
  const [topNav, setTopNav] = useState("topnav");

  const navBarToggleHandler = () => {
    if (topNav === "topnav") {
      setTopNav("topnav responsive");
    } else {
      setTopNav("topnav");
    }
  };

  useEffect(() => {
    console.log();
  });

  return (
    <div className={topNav} id="myTopnav">
      <div className="logoContainer">
        <img src={GDLogo} alt="GorillaDevs Logo" style={{ height: 30 }} />
        GDLAUNCHER
      </div>
      <div>
        <Link to="/" className={location.pathname === "/" && "active"}>
          <i className="fa fa-home" />
          HOME
        </Link>
        <Link
          to="/download"
          className={location.pathname === "/download" && "active"}
        >
          <i class="fas fa-download" />
          DOWNLOAD
        </Link>
        <Link
          to="/explore"
          className={location.pathname === "/explore" && "active"}
        >
          <i className="fas fa-car-side" />
          EXPLORE
        </Link>
        <Link
          to="/blog"
          className={location.pathname === "/blog" && "active"}
        >
          <i class="fas fa-newspaper" />
          BLOG
        </Link>
        <a
          href="javascript:void(0);"
          className="icon"
          onClick={() => navBarToggleHandler(topNav, setTopNav)}
        >
          <i class="fa fa-bars" />
        </a>
      </div>
    </div>
  );
}
export default Navbar;
