import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Download.css";
import { Button } from "antd";

function Download(props) {
  return (
    <div className="download-header">
      <div
        style={{
          fontWeight: 900,
          fontSize: 35,
          textAlign: "center",
          fontStyle: "bold",
          color: "#dfe6e9"
        }}
      >
        Download
      </div>
      <div
        style={{
          fontWeight: 500,
          fontSize: 17,
          textAlign: "center",
          paddingTop: 20
        }}
      >
        <span style={{ color: "#dfe6e9" }}>
          Choose the download for your operating system below
        </span>
      </div>
      <div className="download-buttons">
        <a href="https://github.com/gorilla-devs/GDLauncher/releases/download/v0.8.8/GDLauncher-win-setup.exe">
          <Button type="primary">
            <i class="fab fa-windows" />
            Windows
          </Button>
        </a>
        <a href="https://github.com/gorilla-devs/GDLauncher/releases/download/v0.8.8/GDLauncher-mac-setup.dmg">
          <Button type="primary">
            <i class="fab fa-apple" />
            Mac
          </Button>
        </a>
        <a href="https://github.com/gorilla-devs/GDLauncher/releases/download/v0.8.8/GDLauncher-linux-setup.AppImage">
          <Button type="primary">
            <i class="fab fa-linux" />
            Linux
          </Button>
        </a>
      </div>
    </div>
  );
}
export default Download;
