import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Homepage.css";
import { Button } from "antd";

function Homepage(props) {
  return (
    <div className="homepage-header">
      <div
        style={{
          fontWeight: 900,
          fontSize: 50,
          textAlign: "center",
          fontStyle: "bold",
          color: "#dfe6e9"
        }}
      >
        GDLauncher
      </div>
      <div
        style={{
          fontWeight: 500,
          fontSize: 20,
          textAlign: "center",
          paddingTop: 20
        }}
      >
        <span style={{ color: "#dfe6e9" }}>
          A HANDCRAFTED MINECRAFT CUSTOM LAUNCHER
        </span>
      </div>
      <div className="header-buttons">
        <Link to="/download" style={{ fontSize: 19, color: "white" }}>
          <Button
            type="primary"
            style={{
              width: 120,
              height: 50
            }}
          >
            <i className="fas fa-download" /> Download
          </Button>
        </Link>
        <Link to="/explore" style={{ fontSize: 19, color: "white" }}>
          <i className="fas fa-car-side" /> Explore
        </Link>
      </div>
    </div>
  );
}
export default Homepage;
